html {
  font-size: 10px;
}
body {
  font-size: 1.6rem;
  font-family: 'Arial';
  color: #666666;
  text-rendering: optimizeLegibility;
}
.container {
  max-width: 980px;
  margin: 0 auto;
}
.anchor {
  display: none;
}
.main-nav {
  background-color: #009999;
  padding: 20px 25px;
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.main-logo {
  display: inline;
  float: left;
  width: 25%;
  margin: 0 0%;
  max-width: 170px;
  text-align: center;
}
.main-logo i {
  display: inline-block;
  font-size: 3.6rem;
  color: #ffffff;
  margin-right: 15px;
}
.main-logo-title {
  display: inline;
  font-size: 3.6rem;
  color: #ffffff;
  margin: 0;
}
.main-search {
  display: inline;
  float: left;
  width: 33.33333333%;
  margin: 0 0%;
  position: relative;
}
.main-search:after {
  content: "\f002";
  font-family: "FontAwesome";
  position: absolute;
  font-size: 2.6rem;
  display: inline-block;
  top: 10px;
  left: 30px;
}
.search {
  display: block;
  height: 40px;
  outline: 0;
  margin-left: 15px;
  min-width: 315px;
  border-radius: 12px;
  padding-left: 50px;
  padding-right: 20px;
  border: 1px solid #404040;
}
.main-menu {
  display: inline;
  float: left;
  width: 41.66666667%;
  margin: 0 0%;
  float: right;
  padding-left: 0;
  margin-top: 12px;
  text-align: center;
}
.main-menu a {
  text-decoration: none;
}
.main-menu a:last-child li {
  border-right: 0;
}
.main-menu-item {
  color: #ffffff;
  font-size: 1.8rem;
  display: inline-block;
  padding: 0 25px;
  border-right: 1px solid #ffffff;
}
@media (max-width: 768px) {
  .anchor {
    display: block;
  }
  .main-nav {
    position: fixed;
    height: 100%;
    transform: translateX(-768px);
  }
  .menu-active .main-nav {
    transform: translateX(0);
  }
}
